function mainSlider() {
    var swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        pagination: {
            el: '.main-slider .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        on: {
            afterInit: function () {
                setSliderHeight()
            },
            slideChangeTransitionEnd: function () {
                setSliderHeight()
            },
        },
    })

    function setSliderHeight() {
        let sliderHeight = document.querySelector(".main-slider .swiper-slide-active > div").getBoundingClientRect().height
        // let sliderHeight = $(".main-slider .swiper-slide-active").getBoundingClientRect().height
        console.log(sliderHeight)
        if(window.innerWidth <= 577){
            $('.main-slider .swiper-container').css("height",`${sliderHeight}px`)
        }
    }
}

function Sliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll("._slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)

        SlidersArray[index] = new Swiper(`._slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,

            navigation: {
                nextEl: `._slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `._slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `._slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })
    })
}

