let circleTexts = document.querySelectorAll(".circle-text")

circleTexts.forEach(text => {
    new CircleType(text);

})

$(".favourite-btn,.cart-btn").click(function () {
    $(this).toggleClass("_added")
})

$(".color-field__item").click(function () {
    $(this).siblings(".color-field__item").removeClass("_active")
    $(this).addClass("_active")
})



$(".room-item").click(function () {
    $(".room-item").removeClass("_show")
    $(this).addClass("_show")

    if(window.innerWidth<=576){
        $(".room").addClass("_show-bg")
        $(this).addClass("_show-mob")
        $(".m-page").addClass("fixed-body")
    }
})

$(".room__bg").click(function () {
    $(".room").removeClass("_show-bg")
    $(".room-item").removeClass("_show-mob")
    $(".m-page").removeClass("fixed-body")
})
