$("._menu-item").hover(onIn, onOut);

function onIn() {
    if (window.innerWidth > 992) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                el.addClass("_show")
            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 992) {
        $(this).removeClass("_show")
    }
}

$(".nav__show").click(function () {
    if (window.innerWidth <= 992){
        $(this).parents(".nav__item").toggleClass("_show")
        $(this).siblings(".nav__hidden").slideToggle()
    }
})

$("._toggle-mob-menu").click(function () {
    $(".header").toggleClass("_open-mob-menu")
    $(".m-page").toggleClass("fixed-body")
})

$("._toggle-catalog-menu").click(function () {
    $(".header").toggleClass("_open-catalog-menu")
    $(".m-page").toggleClass("fixed-body")
})

$(".header-city__show").click(function () {
    $(this).parents(".header-city").toggleClass("_show")
})

$(".header-city__item").click(function () {
    let parent = $(this).parents(".header-city")

    $(this).siblings(".header-city__item").removeClass("_active")
    $(this).addClass("_active")
    $(parent).removeClass("_show")
    $(parent).find(".header-city__current").html($(this).html())
})

$(document).mouseup( function(e){
    let div = $( ".header-city" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_show")){
            $(div).removeClass("_show")
        }
    }
});

$(window).on('load resize', function(){
    if ($(window).width() > 992 || $(window).width() <= 576) {
        $('.header .header-city').insertBefore('.nav');
    }
    else{
        $('.header .header-city').insertBefore('.header-contacts');
    }
});

$("._search-field").on("input",function () {
    if($(this).val().length > 3){
        $(this).parents(".m-search").addClass("_show")
    }
})

$(document).mouseup( function(e){
    var div = $( ".m-search" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_show")){
            $(div).removeClass("_show")
        }
    }
});
